import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, BtnEdit, BtnDelete } from 'components/common/button';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { teachers } from 'dump/data';

const columns = [
  {
    title: 'M cn b',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'H v tn',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Hc hm',
    dataIndex: 'academicRank',
    key: 'academicRank',
  },
  {
    title: 'Hc v',
    dataIndex: 'degree',
    key: 'degree',
  },
  {
    title: 'Chc danh',
    dataIndex: 'title',
    key: 'title',
  },
  {
    title: 'Chc v',
    dataIndex: 'position',
    key: 'position',
  },
  // {
  //   title: 'Not Active',
  //   dataIndex: 'notActive',
  //   key: 'notActive',
  // },
  {
    title: 'Thao tc',
    dataIndex: 'operation',
    render: (text, record) =>
      teachers.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#teacher/form/edit/${record.code}`} />
          <Popconfirm 
            title="Bn c mun xa?" 
            okText="Xa"
            cancelText="Hy"
            onConfirm={() => this.handleDelete(record.key)}
          >
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ListTeacherPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Danh sch gio vin">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/teacher/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={teachers} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTeacherPage />
  </Page>
);



